import React, { useMemo } from 'react'
import { AiOutlineMenu, AiOutlineClose } from "react-icons/ai";
import {
  Drawer,
  DrawerBody,
  DrawerFooter,
  DrawerContent,
  useDisclosure,
  Icon,
  Flex,
} from "@chakra-ui/react"
import { Link } from 'react-scroll'

import Box from '../components/Box';
import Button from '../components/Button';
import theme, { Media, responsive } from '../components/ThemeProvider/theme';
import useCsvButton from './useCsvButton'
import { trackClick } from '../utils/trackEvent';

const links = [
  {
    name: '兩大焦點',
    to: 'section-focus'
  },
  {
    name: '永續三贏',
    to: 'section-subject'
  },
  {
    name: '基金檔案',
    to: 'section-fund'
  },
]

const offset = 78

const Menu = ({ overBanner, modalonOpen }) => {
  const buttons = useCsvButton()
  const videos = useMemo(() => buttons.data?.filter(d => d.id === 'header-vid') || [], [buttons])
  const { isOpen, onOpen, onClose } = useDisclosure()
  return (
    <>
      <Media greaterThanOrEqual="tablet">
        <Flex color={overBanner ? 'custom.headerTextGreen' : 'white'} alignItems="center">
          {videos.length > 0 && (
            <Box mx="1.875em">
              <Button.Transparent
                onClick={() => {
                  modalonOpen()
                  trackClick(videos[0]?.name)
                }}
                fontSize="1.125em"
                py="0"
                px="0"
              >
                {videos[0]?.name}
              </Button.Transparent>
            </Box>
          )}
          {links.map(({ name, to }) => (
            <Box.Relative cursor="pointer" fontSize="1.125em" fontWeight="bold" mx="1.875em">
              <Link
                onClick={() => {
                  trackClick(name)
                }}
                smooth
                to={to}
                offset={-offset}
              >{name}</Link>
            </Box.Relative>
          ))}
          <Box mx="1.875em">
            <Button.Orange
              as={Link}
              cursor="pointer"
              smooth
              to="section-buy"
              fontSize="1.125em"
              borderRadius="1.25em"
              offset={-offset}
              onClick={() => trackClick('申購請洽')}
            >申購請洽</Button.Orange>
          </Box>
        </Flex>
      </Media>
      <Media lessThan="tablet">
      <Box.Relative
        as={Button}
        borderRadius="0"
        bg={isOpen ? 'custom.blue' : 'transparent'}
        color={isOpen ? 'white' : overBanner ? 'custom.blue' : 'white'}
        _hover={{
          bg: 'custom.blue',
          color: 'white',
        }}
        _focus={{
          bg: 'transparent',
          color: overBanner ? 'custom.blue' : 'white',
        }}
        onClick={onOpen}
        height={theme.headerHeight}
        width={theme.headerHeight}
        px="0"
        aria-label="選單"
      >
        <Icon as={isOpen ? AiOutlineClose : AiOutlineMenu} w={responsive('1.25rem', '2rem')} h={responsive('1.25rem', '2rem')} />
      </Box.Relative>
      <Drawer
        isOpen={isOpen}
        placement="right"
        onClose={onClose}
        size="md"
        preserveScrollBarGap
      >
        <DrawerContent bg="custom.blue" style={{ top:theme.headerHeight }} height="auto">
          <DrawerBody height="100%">
            {videos.length > 0 && (
              <Box>
                <Button.Transparent
                  width="100%"
                  py="1.5rem"
                  borderBottom="1px solid white"
                  borderRadius="0"
                  onClick={() => {
                    modalonOpen()
                    trackClick(videos[0]?.name)
                  }}
                >
                  {videos[0]?.name}
                </Button.Transparent>
              </Box>
            )}
            {links.map(({ name, to }, i) => (
              <Box key={i}>
                <Button.Transparent
                  width="100%"
                  offset={-offset}
                  py="1.5rem"
                  as={Link}
                  smooth
                  to={to}
                  borderBottom="1px solid white"
                  borderRadius="0"
                  onClick={() => {
                    trackClick(name)
                    onClose()
                  }}
                >{name}</Button.Transparent>
              </Box>
            ))}
          </DrawerBody>
          <DrawerFooter>
            <Button.Orange
              as={Link}
              smooth
              to="section-buy"
              width="100%"
              fontSize="1em"
              py="1rem"
              offset={-offset}
              onClick={() => {
                trackClick('申購請洽')
                onClose()
              }}
            >申購請洽</Button.Orange>
          </DrawerFooter>
        </DrawerContent>
      </Drawer>
      </Media>
    </>
  )
}

export default Menu
